
.articles {
    width: 70%;
    margin-top: -200px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: auto;
    margin-bottom: 300px;
    // border: 5px solid red
    gap: 25px;
    a {
        text-decoration: none;
        color: #aaaaaa;
        // border: 5px solid red
        width: 300px;
        height: 500px;
        animation: enlarge-new-item 300ms ease-in-out 0s 1 normal forwards;
        .article {
            margin: 0 auto;
            background-color: #ffffff;
            max-width: 300px;
            height: 500px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            .article-image-div {
                position: relative;
                width: 300px;
                height: 300px;
                overflow: hidden;
                img {
                    position: absolute;
                    min-height: 300px;
                    // min-width: 300px
                    height: 100%;
                    left: 50%;
                    top: 50%;
                    transform: translateX(-50%) translateY(-50%);
                    opacity: 0.9; } } }
        .text-container {
            padding: 25px;
            padding-top: 10px;
            // display: flex
            h2 {
                font-style: italic;
                font-weight: 200;
                font-size: 18px;
                margin-top: 15px; } }
        &:hover {
            color: #333333;
            box-shadow: 5px 5px 5px 5px rgba(0,0,0,0.1);
            .article {
                .article-image-div {
                    img {
                        opacity: 1; } } } } } }

@keyframes enlarge-new-item {
    from {
        transform: scale(0); }

    to {
        transform: scale(1); } }
