@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@300;500;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Piazzolla:ital,wght@0,100;0,400;0,500;0,700;0,900;1,100;1,200&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.articles{width:70%;margin-top:-200px;display:grid;grid-template-columns:1fr 1fr 1fr;grid-template-rows:auto;margin-bottom:300px;grid-gap:25px;gap:25px}.articles a{text-decoration:none;color:#aaaaaa;width:300px;height:500px;animation:enlarge-new-item 300ms ease-in-out 0s 1 normal forwards}.articles a .article{margin:0 auto;background-color:#ffffff;max-width:300px;height:500px;display:flex;flex-direction:column;justify-content:space-between}.articles a .article .article-image-div{position:relative;width:300px;height:300px;overflow:hidden}.articles a .article .article-image-div img{position:absolute;min-height:300px;height:100%;left:50%;top:50%;transform:translateX(-50%) translateY(-50%);opacity:0.9}.articles a .text-container{padding:25px;padding-top:10px}.articles a .text-container h2{font-style:italic;font-weight:200;font-size:18px;margin-top:15px}.articles a:hover{color:#333333;box-shadow:5px 5px 5px 5px rgba(0,0,0,0.1)}.articles a:hover .article .article-image-div img{opacity:1}@keyframes enlarge-new-item{from{transform:scale(0)}to{transform:scale(1)}}

.Header{width:100vw;height:70vh;background-image:url(/oko-articles-react/static/media/reader.8b6ca600.jpg);background-size:cover;background-position-y:50%}.Header .header-texts{padding:100px;color:#ffffff;text-shadow:1px 1px #333333}.Header .header-texts h1{font-size:150px;margin:0}.Header .header-texts h2{font-size:35px;margin:0}

*{margin:0;padding:0;box-sizing:border-box;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}body{background-color:#dddddd}.App{font-family:'Piazzolla', serif;width:100%;display:flex;flex-direction:column;align-items:center}

